* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
};

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
};

a {
  color: #333;
  text-decoration: none;
};

.container {
  padding: 0 1rem;
}

.btn {
  display: inline-block;
  border: none;
  background: #555;
  color: white;
  padding: 7px 20px;
  cursor: pointer;
}

.btn:hover{
  background: #666;
}